<template>
  <div>
    <grid-toolbar toolbarTitle="Discover Devices">
      <template v-slot:gridCounter>
        <header-selector
          :grid-config-change="gridConfigChange"
          :fieldKeys="fieldKeys"
          :tableName="tableName"
        ></header-selector>
        <grid-items-counter :num="selectedItems.length"></grid-items-counter>
      </template>
      <template v-slot:search>
        <v-text-field
          v-model="search"
          append-icon="mdi-magnify"
          label="Search"
          single-line
          hide-details
          class="pl-10"
        />
      </template>
      <template v-slot:customAction>
        <v-btn x-small class="mr-1 btn-height-22 white--text" color="#8cd0ab" @click="() => createSlaves(selectedItems)":disabled="selectedItems.length == 0">Add as slave<v-icon x-small right>mdi-plus</v-icon></v-btn>
      </template>
    </grid-toolbar>
    <v-data-table
      v-model="selectedItems"
      item-key="global_uuid"
      show-select
      :loading="isFetching"
      :search="search"
      fixed-header
      :height="tableHeight"
      :headers="headers"
      :items="dataItems"
      sort-by="calories"
      class="elevation-1"
      :footer-props="tableProps.FOOTER_PROPS"
      :itemsPerPage="tableProps.ITEMS_PER_PAGE"
    >
      <template v-slot:item.client_name="{ item }">
        <span>
          <master-star-indicator :is-master="item.is_master"></master-star-indicator>
          <span>{{ item.client_name }}</span>
        </span>
      </template>
      <template v-slot:item.actions="{ item }">
        <v-btn
          x-small
          class="btn-height-22 white--text"
          color="#8cd0ab"
          @click="() => createSlaves(item)"
          :loading="item.isUpdating"
          v-if="item.isAddToSlaveVisible"
        >
          Add as slave
        </v-btn>
        <div v-else>No actions available</div>
      </template>
    </v-data-table>
  </div>
</template>

<script>
import GridToolbar from '../Common/GridToolbar';
import { DATA_TABLE_PROPS } from '../../constants/appConstant';
import GridItemsCounter from '../Common/gridItemsCounter.vue';
import MasterStarIndicator from '../Common/MasterStarIndicator.vue';
import HeaderSelector from '../HeaderSelector/headerSelector.vue';
import * as headerBuilderService from '../../services/headerBuilderService';
import * as gridTableHeadersService from '../../services/gridTableHeadersService';
import * as gridNames from '../../constants/gridNames';

const MAX_VISIBLE_GRID_ITEMS = 20;
export default {
  name: 'discover-devices',
  components: { GridToolbar, MasterStarIndicator, GridItemsCounter, HeaderSelector },
  props: ['items', 'createSlaves', 'isFetching', 'itemKeys'],
  data: () => ({
    search: '',
    dialog: false,
    tableName: gridNames.DISCOVER_SLAVE_GRID,
    isFetchingDevices: false,
    selectedItems: [],
    headers: [],
    tableProps: { ...DATA_TABLE_PROPS },
  }),
  mounted() {
    this.fetchSetGridHeaders();
  },
  computed: {
    dataItems() {
      return this.items.map((item, index) => ({
        ...item,
        sn: index + 1,
        client_name: item.is_master ? `   ${item.client_name}` : item.client_name,
      }));
    },
    fieldKeys() {
      return this.itemKeys;
    },
    tableHeight() {
      return this.items && this.items.length > MAX_VISIBLE_GRID_ITEMS ? '75vh' : undefined;
    },
  },
  methods: {
    gridConfigChange() {
      this.fetchSetGridHeaders();
    },
    fetchSetGridHeaders() {
      gridTableHeadersService
        .fetch(gridNames.DISCOVER_SLAVE_GRID)
        .then(gridConfig => {
          const gridHeaders = headerBuilderService.gridHeadersResolver(gridNames.DISCOVER_SLAVE_GRID, gridConfig);
          this.headers = headerBuilderService.buildHeaders()(gridHeaders);
        })
        .catch(err => {
          this.headers = gridNames.DEFAULT_HEADERS[gridNames.DISCOVER_SLAVE_GRID];
        });
    },
  },
};
</script>
<style>
.v-data-table__wrapper::-webkit-scrollbar {
  width: 10px;
}

.v-data-table__wrapper::-webkit-scrollbar-track {
  background: #202020;
  border-left: 1px solid #2c2c2c;
}

.v-data-table__wrapper::-webkit-scrollbar-thumb {
  background: #3e3e3e;
  border: solid 2px #202020;
  border-radius: 6px;
}

.v-data-table__wrapper::-webkit-scrollbar-thumb:hover {
  background: white;
}
</style>
