import * as gridConstant from '../constants/gridNames';

const defaultConfig = {
  hasActions: true,
  hasSerialNumber: true,
  actionValue: 'actions',
  snValue: 'sn',
};

const getHeader = ({ text = '', align = 'center', sortable = true, value = null }) => {
  return {
    text,
    align,
    sortable,
    name: value,
    value: value,
  };
};

const capitalize = (string = '') => {
  const lower = string.toLowerCase();
  return string.charAt(0).toUpperCase() + lower.slice(1);
};

export const extractNameFromField = field => {
  return field
    .split('_')
    .map(val => capitalize(val))
    .join(' ');
};

export const buildHeaders = (config = defaultConfig) => (headers = []) => {
  const tableHeaders = [];

  headers.forEach(header => {
    tableHeaders.push(getHeader({ text: extractNameFromField(header.displayName), value: header.name }));
  });

  // input [{name: 'device_name', displayName: 'Device Name'}, {name:'site_name', displayName: 'Site Name'}, 'site_lat': 'Site Lat', 'site_lon', 'site_address', 'count']

  if (config.hasActions) {
    tableHeaders.push(getHeader({ text: 'Actions', value: config.actionValue }));
  }
  return tableHeaders;
};

export const gridHeadersResolver = (gridName, gridConfig) => {
  const defaultHeaders = gridConstant.DEFAULT_HEADERS[gridName];
  let gridHeaders = defaultHeaders;
  if (gridConfig) {
    gridHeaders = gridConfig.headers.filter(header => header.isEnabled);
    if (gridHeaders.length == 0) {
      gridHeaders = defaultHeaders;
    }
  }
  const extraGridHeaders = gridConstant.EXTRA_GRID_HEADERS[gridName] || [];

  return gridHeaders
    .map(header => ({ name: header.name, displayName: header.displayName || header.name }))
    .concat(...extraGridHeaders);
};
