<template>
  <div>
    <page-title title="Master Slave Mapping">
      <template v-slot:spinner>
        <v-progress-circular
          v-if="isFetchingDevices || isFetchingSlaveDevices"
          indeterminate
          size="20"
        ></v-progress-circular>
      </template>
    </page-title>
    <v-tabs v-model="tab" right>
      <v-tab :key="0">Discover Devices </v-tab>
      <v-tab :key="1">Slave Devices </v-tab>
    </v-tabs>
    <v-tabs-items v-model="tab">
      <v-tab-item :key="0">
        <discover-devices
          :items="discoveredDevices"
          :item-keys="discoveredDevicesItemKeys"
          :create-slaves="createSlaves"
          :is-fetching="isFetchingDevices"
        ></discover-devices>
      </v-tab-item>
      <v-tab-item :key="1">
        <slave-devices
          :items="slaveDevices"
          :item-keys="slaveDevicesItemKeys"
          :removeSlaves="removeSlaves"
          :is-fetching="isFetchingSlaveDevices"
          :switch-to-slave="switchToSlave"
          :app-slave-id="appSlaveId"
          :remove-app-slave-id="removeAppSlaveId"
        ></slave-devices>
      </v-tab-item>
    </v-tabs-items>
  </div>
</template>
<script>
import PageTitle from '../Common/PageTitle';
import SlaveDevices from './SlaveDevices';
import DiscoverDevices from './DiscoverDevices';
import * as slaveDeviceService from '../../services/slaveDevicesService';
import * as slaveAppsUiService from '../../services/slaveAppsUiService';
export default {
  name: 'master-slave-page',
  components: {
    PageTitle,
    DiscoverDevices,
    SlaveDevices,
  },
  watch: {
    '$route.query.tab': function (val) {
      this.tab = this.tabs.indexOf(val);
    },
    'tab': function (val, oldVal) {
      const { tab } = this.$route.query;
      if (tab !== this.tabs[val]) {
        const tabKey = this.tabs[val];
        this.$router.push({ path: 'master-slaves', query: { tab: tabKey } });
      }
    },
  },
  computed: {
    isItemKeysSet() {
      return this.slaveDevicesItemKeys.length > 0 && this.slaveDevicesItemKeys.length > 0;
    },
  },
  data() {
    return {
      tab: 0,
      appSlaveId: null,
      tabs: ['discover-devices', 'slave-devices'],
      slaveDevices: [],
      discoveredDevicesItemKeys: [],
      slaveDevicesItemKeys: [],
      devicesMap: {},
      slaveDevicesMap: {},
      discoveredDevices: [],
      isFetchingSlaveDevices: false,
      isFetchingDevices: false,
      timeout: null,
    };
  },
  mounted() {
    const { tab: tabKey } = this.$route.query;
    this.tab = this.tabs.indexOf(tabKey);
    this.getAppSlaveId();
  },
  created() {
    this.syncDevices();
    this.getAppSlaveId();
  },
  destroyed() {
    clearTimeout(this.timeout);
  },
  methods: {
    formatAddress(item) {
      let address = '';
      if (item.site_address) {
        address += item.site_address;
      }

      if (item.site_city) {
        address += ', ' + item.site_city;
      }

      if (item.site_country) {
        address += ', ' + item.site_country;
      }

      return address;
    },
    async syncDevices() {
      this.isFetchingSlaveDevices = true;
      this.isFetchingDevices = true;
      await this.fetchSlaveDevices();
      await this.fetchDevices();
      this.getAppSlaveId();
    },
    fetchSlaveDevices() {
      return slaveDeviceService
        .fetch()
        .then(data => {
          this.slaveDevicesMap = data;
          const slaveDeviceKeys = Object.keys(data);
          this.setSlaveItemKeys(data, slaveDeviceKeys);
          this.slaveDevices = slaveDeviceKeys.map(key => ({
            ...this.slaveDevicesMap[key],
            isDeleting: false,
            site_address: this.formatAddress(data[key]),
          }));
        })
        .catch(err => {
          // this.$toasted.show('Error fetching slave devices', { type: 'error' });
        })
        .finally(() => {
          this.isFetchingSlaveDevices = false;
        });
    },
    setDiscoveredItemKeys(data, discoverDeviceKeys) {
      this.discoveredDevicesItemKeys = Object.keys(
        (Array.isArray(discoverDeviceKeys) && discoverDeviceKeys.length > 0 && data[discoverDeviceKeys[0]]) || {},
      );
      this.discoveredDevicesItemKeys.unshift('sn');
    },
    setSlaveItemKeys(data, slaveDeviceKeys) {
      this.slaveDevicesItemKeys = Object.keys(
        (Array.isArray(slaveDeviceKeys) && slaveDeviceKeys.length > 0 && data[slaveDeviceKeys[0]]) || {},
      );
      this.slaveDevicesItemKeys.unshift('sn');
    },
    fetchDevices() {
      return slaveDeviceService
        .discoverDevices()
        .then(data => {
          this.devicesMap = data;
          const discoverDeviceKeys = Object.keys(this.devicesMap);
          this.setDiscoveredItemKeys(data, discoverDeviceKeys);
          this.discoveredDevices = discoverDeviceKeys.map(key => ({
            ...this.devicesMap[key],
            isUpdating: false,
            isAddToSlaveVisible: !this.slaveDevicesMap[key],
            site_address: this.formatAddress(this.devicesMap[key]),
          }));
        })
        .catch(err => {
          this.$toasted.show('Error fetching remote devices', { type: 'error' });
        })
        .finally(() => {
          this.isFetchingDevices = false;
        });
    },
    createSlaves(slave) {
      slave.isUpdating = true;
      let createSlaveService;
      if (Array.isArray(slave) && slave.length > 0) {
        createSlaveService = slaveDeviceService.bulkCreate;
      } else {
        createSlaveService = slaveDeviceService.create;
      }
      return createSlaveService(slave)
        .then(() => {
          this.$toasted.show('Successfully updated the device status.');
          this.syncDevices();
        })
        .catch(() => {
          this.$toasted.show('Error occured while updating the device status', { type: 'error' });
        })
        .finally(() => {
          slave.isUpdating = false;
        });
    },
    removeSlaves(slave) {
      slave.isDeleting = true;

      let removeSlaveService;
      if (Array.isArray(slave) && slave.length > 0) {
        removeSlaveService = slaveDeviceService.bulkRemove;
      } else {
        removeSlaveService = slaveDeviceService.remove;
      }
      return removeSlaveService(slave)
        .then(() => {
          this.$toasted.show('Successfully remove the device as slave.');
          this.syncDevices();
        })
        .catch(() => {
          this.$toasted.show('Error occured while updating the device status', { type: 'error' });
        })
        .finally(() => {
          slave.isDeleting = false;
        });
    },
    switchToSlave(slave) {
      this.$store.commit('changeSwitchedSlaveState', false);
      this.timeout = setTimeout(() => {
        slaveAppsUiService.setAppSlaveId(slave.global_uuid, slave);
        this.$store.commit('changeSwitchedSlaveState', true);
        this.appSlaveId = slave.global_uuid;
        this.$store.commit('changeAppRerenderCount');
      }, 100);
    },
    getAppSlaveId() {
      this.appSlaveId = slaveAppsUiService.getAppSlaveId();
    },
    removeAppSlaveId() {
      this.appSlaveId = null;
      this.$store.commit('changeSwitchedSlaveState', false);
      slaveAppsUiService.removeAppSlaveId();
      this.$store.commit('changeAppRerenderCount');
    },
  },
};
</script>
